import React, { useState, useEffect } from "react";
import {
  Box, Button, Typography, Select, MenuItem, FormControl, InputLabel, Dialog, useMediaQuery
} from "@mui/material";
import axios from "axios";
import MAP_SRC from "./map-leo.jpg";
import MAP_pool from "./pool.jpg";
import './style.css'

// Соотношение сторон карты (замени, если другое)
const MAP_ASPECT = 839 / 768;

function findAllRoutes(points, edges, startId, endId, maxDepth = 20) {
  const routes = [];
  function dfs(curr, path, edgePath, depth) {
    if (curr === endId) {
      routes.push({ pointPath: [...path, curr], edgePath: [...edgePath] });
      return;
    }
    if (depth > maxDepth) return;
    for (const edge of edges.filter(e => e.from === curr)) {
      if (path.includes(edge.to)) continue;
      dfs(edge.to, [...path, curr], [...edgePath, edge], depth + 1);
    }
  }
  dfs(startId, [], [], 0);
  return routes;
}

export default function UserMap() {
  const [points, setPoints] = useState([]);
  const [edges, setEdges] = useState([]);
  const [startId, setStartId] = useState(null);
  const [endId, setEndId] = useState(null);

  const [allRoutes, setAllRoutes] = useState([]);
  const [routeIdx, setRouteIdx] = useState(0);

  const [modalOpen, setModalOpen] = useState(false);
  const [modalPoint, setModalPoint] = useState(null);

  const [buildMode, setBuildMode] = useState(false);

  const isMobile = useMediaQuery('(max-width:600px)');
  const pointSize = isMobile ? 20 : 20;

  useEffect(() => {
    fetchPoints();
    fetchEdges();
  }, []);

  const fetchPoints = async () => {
    const { data } = await axios.get("http://91.199.45.137/site/points");
    setPoints(data);
  };
  const fetchEdges = async () => {
    const { data } = await axios.get("http://91.199.45.137/site/edges");
    setEdges(data);
  };

  // Поиск маршрутов между двумя точками
  useEffect(() => {
    if (startId && endId && startId !== endId && buildMode) {
      const all = findAllRoutes(points, edges, startId, endId);
      setAllRoutes(all);
      setRouteIdx(0);
    } else {
      setAllRoutes([]);
      setRouteIdx(0);
    }
  }, [startId, endId, points, edges, buildMode]);

  const currentRoute = allRoutes[routeIdx] || null;
  const currentEdges = currentRoute ? currentRoute.edgePath : [];

  const routeNames = allRoutes.map((r, i) => {
    const uniqueNames = Array.from(new Set(r.edgePath.map(e => e.name || ""))).filter(Boolean);
    if (uniqueNames.length === 1) return uniqueNames[0];
    return `Маршрут ${i + 1}`;
  });

  // Клик по точке
  const handlePointClick = (pt) => {
    if (!buildMode) {
      setModalPoint(pt);
      setModalOpen(true);
      return;
    }
    // Режим построения маршрута
    if (!startId || (startId && endId)) {
      setStartId(pt.id);
      setEndId(null);
    } else if (pt.id !== startId) {
      setEndId(pt.id);
    }
  };

  // Запуск режима построения маршрута
  const handleStartBuild = () => {
    setBuildMode(true);
    setStartId(null);
    setEndId(null);
    setAllRoutes([]);
    setRouteIdx(0);
  };

  // Сброс маршрута и выход из режима
  const handleReset = () => {
    setBuildMode(false);
    setStartId(null);
    setEndId(null);
    setAllRoutes([]);
    setRouteIdx(0);
  };

  return (
    <Box>
      <Typography variant="h5" sx={{ mb: 2 }}>Пошук маршруту</Typography>
      <Typography variant="body2" sx={{ mb: 1, color: "#888" }}>
        Для побудови маршруту натисніть "Побудувати маршрут", після чого оберіть дві точки.
        Клік по точці поза цим режимом відкриває деталі.
      </Typography>

      {!buildMode ? (
        <Button variant="contained" sx={{ mr: 2, mb: 2 }} onClick={handleStartBuild}>
          Побудувати маршрут
        </Button>
      ) : (
        <Button variant="outlined" color="error" sx={{ mr: 2, mb: 2 }} onClick={handleReset}>
          Вийти з режиму
        </Button>
      )}

      <Box
        sx={{
          width: "95vw",
          maxWidth: 900,
          aspectRatio: `${MAP_ASPECT}`,
          position: "relative",
          mx: "auto",
          mb: 3,
          border: "2px dashed #4475ff44",
          background: "#fff",
          borderRadius: 3,
          overflow: "hidden",
          "@media (max-width: 600px)": {
            minWidth: 0,
            maxWidth: "100vw",
          },
        }}
      >
        <img
          src={MAP_SRC}
          alt="Карта"
          style={{
            width: "100%",
            height: "100%",
            display: "block",
            objectFit: "cover",
            borderRadius: 12,
            pointerEvents: "none",
            userSelect: "none"
          }}
          draggable={false}
        />

        {/* SVG слой для всех линий маршрута */}
        <svg
          style={{
            position: "absolute",
            left: 0,
            top: 0,
            width: "100%",
            height: "100%",
            pointerEvents: "none",
            zIndex: 10,
          }}
          viewBox="0 0 100 100"
          preserveAspectRatio="none"
        >
          {currentEdges.map((edge, i) =>
            edge.polyline && edge.polyline.length > 1 ? (
              <polyline
                key={i}
                points={edge.polyline.map(pt => `${pt.x},${pt.y+3}`).join(" ")}
                fill="none"
                stroke="#ff4056"
                strokeWidth={isMobile ? 1.2 : 0.5} // <-- вот здесь тонкие линии!
                strokeDasharray="4,2"
              />
            ) : null
          )}
        </svg>

        {/* Точки на карте */}
        {points.map((pt, idx) => (
          <Box
            key={pt.id || idx}
            sx={{
              position: "absolute",
              left: `calc(${pt.x}% - ${pointSize / 2}px)`,
              top: `calc(${pt.y}% - ${pointSize / 2}px)`,
              width: pointSize,
              height: pointSize,
              borderRadius: "50%",
              backgroundColor:
                pt.id === startId
                  ? "#3de579"
                  : pt.id === endId
                    ? "#ffa500"
                    : "#4475ff",
              border: pt.id === startId || pt.id === endId ? "3px solid #fff" : "2px solid #fff",
              zIndex: 20,
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              color: "#fff",
              fontWeight: "bold",
              cursor: "pointer",
              fontSize: isMobile ? 18 : 16,
              boxShadow: "0 2px 8px #0001",
              userSelect: "none",
              transition: "all .15s"
            }}
            onClick={() => handlePointClick(pt)}
            title={pt.label}
          >
            {pt.id || idx + 1}
          </Box>
        ))}
      </Box>

      {(buildMode && startId && endId && allRoutes.length > 1) && (
        <FormControl sx={{ minWidth: 240, mb: 2 }}>
          <InputLabel>Варіант маршруту</InputLabel>
          <Select
            value={routeIdx}
            label="Варіант маршруту"
            onChange={e => setRouteIdx(Number(e.target.value))}
          >
            {routeNames.map((name, i) => (
              <MenuItem value={i} key={i}>{name}</MenuItem>
            ))}
          </Select>
        </FormControl>
      )}

      {(buildMode && currentRoute && currentRoute.edgePath.some(e => e.name)) && (
        <Typography sx={{ mb: 2, fontStyle: "italic", color: "#666" }}>
          Назва маршруту: {routeNames[routeIdx]}
        </Typography>
      )}

      {/* Модалка с инфой о точке */}
      <Dialog open={modalOpen} onClose={() => setModalOpen(false)} PaperProps={{
        sx: { borderRadius: 3, p: 0, overflow: "visible", minWidth: 320 }
      }}>
        <Box sx={{ px: 3, pt: 2, pb: 3, position: "relative" }}>
          <Typography
            variant="h6"
            sx={{
              color: "#40be3b",
              fontWeight: "bold",
              textAlign: "center",
              mb: 2,
              fontSize: 20,
              mt: 1,
            }}
          >
            Інформація про об'єкт
          </Typography>
          <Button
            size="small"
            onClick={() => setModalOpen(false)}
            sx={{
              position: "absolute", right: 12, top: 12, minWidth: 0, p: 0,
              color: "#444", fontWeight: "bold", fontSize: 22, lineHeight: 1,
              bgcolor: "transparent", "&:hover": { bgcolor: "#f7f7f7" }
            }}
          >×</Button>
          <Box sx={{
            borderRadius: 2, overflow: "hidden", mb: 2, height: 240, width: "100%",
            display: "flex", alignItems: "center", justifyContent: "center",
            background: "#f2f2f2"
          }}>
            <img
              src={modalPoint?.img || MAP_pool}
              alt={modalPoint?.label}
              style={{
                width: "100%", height: "240px", objectFit: "cover", display: "block"
              }}
              onError={e => e.currentTarget.src = MAP_pool}
            />
          </Box>
          {modalPoint && (
            <Box sx={{ fontSize: 17 }}>
              {/* <Box sx={{ display: "flex", gap: 1 }}>
                <span style={{ color: "#40be3b", fontWeight: 500 }}>ID:</span>
                <span style={{ color: "#333" }}>{modalPoint.id}</span>
              </Box> */}
              {modalPoint.label && (
                <Box sx={{ display: "flex", gap: 1 }}>
                  <span className="name" style={{ color: "#40be3b", fontWeight: 500 }}>Назва:</span>
                  <span className="content" style={{ color: "#333" }}>{modalPoint.label}</span>
                </Box>
              )}

              {modalPoint.description && (
                <Box sx={{ display: "flex", gap: 1 }}>
                  <span className="name" style={{ color: "#40be3b", fontWeight: 500 }}>Опис:</span>
                  <span className="content" style={{ color: "#333" }}>{modalPoint.description}</span>
                </Box>
              )}
              <Box sx={{ display: "flex", gap: 1 }}>
                <span className="name" style={{ color: "#40be3b", fontWeight: 500 }}>X:</span>
                <span className="content" style={{ color: "#333" }}>{modalPoint.x}</span>
              </Box>
              <Box sx={{ display: "flex", gap: 1 }}>
                <span className="name" style={{ color: "#40be3b", fontWeight: 500 }}>Y:</span>
                <span className="content" style={{ color: "#333" }}>{modalPoint.y}</span>
              </Box>
            </Box>
          )}
          <Box sx={{ mt: 3, textAlign: "end" }}>
            <Button
              onClick={() => setModalOpen(false)}
              variant="contained"
              sx={{
             
                background: "#40be3b",
                color: "#fff",
                fontWeight: 500,
                borderRadius: 2,
                textTransform: "none",
                px: 4,
                py: 1,
                fontSize: 16,
                "&:hover": { background: "#2fa42b" }
              }}
            >
              Закрити
            </Button>
          </Box>
        </Box>
      </Dialog>
    </Box>
  );
}
