import React, { useState, useEffect, useRef } from "react";
import {
  Box, Button, Typography, Dialog, DialogTitle, DialogContent, DialogActions,
  TextField, IconButton, List, ListItem, ListItemText, useMediaQuery
} from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import EditIcon from "@mui/icons-material/Edit";
import axios from "axios";
import MAP_SRC from "./map-leo.jpg";

const MAP_ASPECT = 839 / 768;
const Y_OFFSET = -2;

export default function AdminMap() {
  const [points, setPoints] = useState([]);
  const [edges, setEdges] = useState([]);
  const [drawing, setDrawing] = useState(false);
  const [polyline, setPolyline] = useState([]);
  const [from, setFrom] = useState(null);
  const [editing, setEditing] = useState(null);
  const [routeName, setRouteName] = useState("");
  const [dragIdx, setDragIdx] = useState(null);
  const [lastMouse, setLastMouse] = useState(null);
  const [hoveredIdx, setHoveredIdx] = useState(null);

  const [editingEdge, setEditingEdge] = useState(null);
  const [editingEdgeName, setEditingEdgeName] = useState("");
  const [buildMode, setBuildMode] = useState(false);

  const ref = useRef();

  const isMobile = useMediaQuery('(max-width:600px)');
  const pointSize = isMobile ? 20 : 20;

  useEffect(() => {
    fetchPoints();
    fetchEdges();
  }, []);

  const fetchPoints = async () => {
    const { data } = await axios.get("http://91.199.45.137/site/points");
    setPoints(data);
  };
  const fetchEdges = async () => {
    const { data } = await axios.get("http://91.199.45.137/site/edges");
    setEdges(data);
  };

  const handlePointCtrlMouseDown = (pt, e, idx) => {
    if (!buildMode) return;
    if (drawing) return;
    setDrawing(true);
    setFrom(pt.id);
    setPolyline([{ x: pt.x, y: pt.y }]);
  };

  const handlePointCtrlMouseUp = async (pt, e, idx) => {
    if (!buildMode) return;
    if (!drawing || from === pt.id) return;
    setDrawing(false);

    const fromPt = points.find(p => p.id === from);
    const toPt = points.find(p => p.id === pt.id);
    if (!fromPt || !toPt) return;

    let pl = polyline.slice();
    pl[0] = { x: fromPt.x, y: fromPt.y };
    pl[pl.length - 1] = { x: toPt.x, y: toPt.y };

    const adjustedPolyline = pl.map(pt => ({
      ...pt,
      y: +((pt.y + Y_OFFSET).toFixed(2))
    }));

    const name = prompt("Введите имя маршрута (опционально):", routeName || "");
    await axios.post("http://91.199.45.137/site/edges", {
      from, to: toPt.id, polyline: adjustedPolyline, name: name || ""
    });

    setRouteName("");
    setFrom(null);
    setPolyline([]);
    setLastMouse(null);
    await fetchEdges();
  };

  const handleCancel = () => {
    setDrawing(false);
    setFrom(null);
    setPolyline([]);
    setLastMouse(null);
  };

  const savePoint = async (data) => {
    await axios.post("http://91.199.45.137/site/points", data);
    await fetchPoints();
  };
  const deletePoint = async (id) => {
    await axios.delete(`http://91.199.45.137/site/points/${id}`);
    await fetchPoints();
    await fetchEdges();
  };

  const deleteEdge = async (edgeId) => {
    await axios.delete(`http://91.199.45.137/site/edges/${edgeId}`);
    await fetchEdges();
  };

  const updateEdgeName = async (edgeId, name) => {
    await axios.patch(`http://91.199.45.137/site/edges/${edgeId}`, { name });
    await fetchEdges();
  };

  const handlePointDragStart = (idx) => setDragIdx(idx);
  const handlePointDragEnd = async (idx) => {
    setDragIdx(null);
    await savePoint(points[idx]);
  };

  const handleMapMouseMove = (e) => {
    if (drawing) {
      const rect = ref.current.getBoundingClientRect();
      let x = ((e.clientX - rect.left) / rect.width) * 100;
      let y = ((e.clientY - rect.top) / rect.height) * 100;
      setPolyline(pl => [...pl, { x: +x.toFixed(2), y: +y.toFixed(2) }]);
      setLastMouse({ x: +x.toFixed(2), y: +y.toFixed(2) });
    }
    if (dragIdx !== null) {
      const rect = ref.current.getBoundingClientRect();
      const x = ((e.clientX - rect.left) / rect.width) * 100;
      const y = ((e.clientY - rect.top) / rect.height) * 100;
      setPoints(points =>
        points.map((pt, i) =>
          i === dragIdx ? { ...pt, x: +x.toFixed(2), y: +y.toFixed(2) } : pt
        )
      );
    }
  };

  const exitBuildMode = () => {
    setBuildMode(false);
    setDrawing(false);
    setFrom(null);
    setPolyline([]);
    setLastMouse(null);
  };

  return (
    <Box>
      <Typography variant="h5" sx={{ mb: 2 }}>
        Режим малювання і редагування
        <span style={{
          fontSize: isMobile ? 13 : 15,
          color: "#888",
          marginLeft: 8,
          display: "block"
        }}>
          {buildMode
            ? "(додавайте маршрут: клік по точці для початку та завершення)"
            : "(переміщення — drag, подвійний клік — додати точку)"}
        </span>
      </Typography>

      {!buildMode ? (
        <Button
          variant="contained"
          sx={{
            mr: 2, mb: 2,
            fontSize: isMobile ? 15 : 16,
            minWidth: isMobile ? 120 : 180,
            py: isMobile ? 1.2 : 1.7
          }}
          onClick={() => setBuildMode(true)}
        >
          Почати малювання маршруту
        </Button>
      ) : (
        <Button
          variant="outlined"
          color="error"
          sx={{
            mr: 2, mb: 2,
            fontSize: isMobile ? 15 : 16,
            minWidth: isMobile ? 120 : 180,
            py: isMobile ? 1.2 : 1.7
          }}
          onClick={exitBuildMode}
        >
          Вийти з режиму малювання
        </Button>
      )}

      <Box
        ref={ref}
        sx={{
          width: "95vw",
          maxWidth: 900,
          aspectRatio: `${MAP_ASPECT}`,
          position: "relative",
          mx: "auto",
          mb: 3,
          border: "2px dashed #4475ff44",
          background: "#fff",
          borderRadius: 3,
          overflow: "hidden",
          "@media (max-width: 600px)": {
            minWidth: 0,
            maxWidth: "100vw",
          },
        }}
        onMouseMove={handleMapMouseMove}
        onMouseUp={e => {
          if (dragIdx !== null && e.button === 0) handlePointDragEnd(dragIdx);
        }}
        onDoubleClick={e => {
          if (e.target.tagName === "IMG") {
            const rect = ref.current.getBoundingClientRect();
            const x = ((e.clientX - rect.left) / rect.width) * 100;
            const y = ((e.clientY - rect.top) / rect.height) * 100;
            setEditing({ x: +x.toFixed(2), y: +y.toFixed(2), label: "", description: "", id: "" });
          }
        }}
      >
        <img
          src={MAP_SRC}
          alt="Карта"
          style={{
            width: "100%",
            height: "100%",
            display: "block",
            objectFit: "cover",
            borderRadius: 12,
            pointerEvents: "none",
            userSelect: "none"
          }}
          draggable={false}
        />

        {/* ---- АДАПТИВНЫЙ SVG-слой маршрутов ---- */}
        <svg
          style={{
            position: "absolute",
            left: 0,
            top: 0,
            width: "100%",
            height: "100%",
            pointerEvents: "none",
            zIndex: 10,
          }}
          viewBox="0 0 100 100"
          preserveAspectRatio="none"
        >
          {/* Готовые маршруты */}
          {edges.map((edge, i) =>
            edge.polyline && edge.polyline.length > 1 ? (
              <g key={i}>
                <polyline
                  points={edge.polyline.map(pt => `${pt.x},${pt.y+3}`).join(" ")}
                  fill="none"
                  stroke="#ff4056"
                  strokeWidth={isMobile ? 1.2 : 0.5}
                  strokeDasharray="4,2"
                />
                {edge.name && (
                  <text
                    x={edge.polyline[0].x}
                    y={edge.polyline[0].y-2}
                    fontSize={isMobile ? 9 : 10}
                    fill="#666"
                    style={{ userSelect: "none" }}
                  >{edge.name}</text>
                )}
              </g>
            ) : null
          )}

          {/* В процессе рисования — линия мышкой (последняя точка — курсор) */}
          {buildMode && drawing && polyline.length > 0 && (
            <polyline
              points={
                [...polyline, lastMouse || polyline[polyline.length - 1]]
                  .map(pt => `${pt.x},${pt.y}`)
                  .join(" ")
              }
              fill="none"
              stroke="#ff4056"
              strokeWidth={isMobile ? 1.2 : 0.5}
              strokeDasharray="4,2"
            />
          )}
        </svg>

        {/* Точки */}
        {points.map((pt, idx) => (
          <Box
            key={pt.id || idx}
            sx={{
              position: "absolute",
              left: `calc(${pt.x}% - ${pointSize / 2}px)`,
              top: `calc(${pt.y}% - ${pointSize / 2}px)`,
              width: pointSize,
              height: pointSize,
              borderRadius: "50%",
              backgroundColor: from === pt.id && buildMode ? "#ffa500" : "#4475ff",
              border: "2px solid #fff",
              zIndex: 20,
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              color: "#fff",
              fontWeight: "bold",
              cursor: "pointer",
              fontSize: isMobile ? 16 : 16,
              boxShadow: "0 2px 8px #0001",
              userSelect: "none",
              transition: "all .15s"
            }}
            draggable={!drawing}
            onMouseDown={e => {
              if (!drawing && e.button === 0) handlePointDragStart(idx);
            }}
            onMouseUp={e => {
              if (dragIdx !== null && e.button === 0) handlePointDragEnd(idx);
            }}
            onClick={e => {
              if (buildMode && hoveredIdx === idx) {
                if (!drawing) handlePointCtrlMouseDown(pt, e, idx);
                else handlePointCtrlMouseUp(pt, e, idx);
              }
            }}
            onDoubleClick={() => setEditing({ ...pt, idx })}
            onMouseEnter={() => setHoveredIdx(idx)}
            onMouseLeave={() => setHoveredIdx(null)}
            title={pt.label}
          >
            {pt.id || idx + 1}
          </Box>
        ))}
      </Box>
      {buildMode && drawing && (
        <Button
          onClick={handleCancel}
          sx={{
            fontSize: isMobile ? 15 : 16,
            py: isMobile ? 1 : 1.5,
            mt: 1
          }}
        >Скасувати</Button>
      )}

      <Box sx={{ mb: 2 }}>
        <Typography variant="h6" sx={{ mt: 2, fontSize: isMobile ? 18 : 21 }}>Список маршрутів (ребер):</Typography>
        <List>
          {edges.map(edge => (
            <ListItem
              key={edge._id || (edge.from + "-" + edge.to)}
              secondaryAction={
                <>
                  <IconButton
                    color="primary"
                    onClick={() => {
                      setEditingEdge(edge);
                      setEditingEdgeName(edge.name || "");
                    }}
                    sx={{ mr: 1 }}
                  >
                    <EditIcon />
                  </IconButton>
                  <IconButton color="error" onClick={() => deleteEdge(edge._id)}>
                    <DeleteIcon />
                  </IconButton>
                </>
              }
            >
              <ListItemText
                primary={
                  <span style={{
                    cursor: "pointer",
                    fontSize: isMobile ? 14 : 16
                  }} onClick={() => {
                    setEditingEdge(edge);
                    setEditingEdgeName(edge.name || "");
                  }}>
                    {edge.from} → {edge.to}
                    {edge.name ? " — " + edge.name : ""}
                  </span>
                }
                secondary={edge.polyline?.length
                  ? `Точек в маршруте: ${edge.polyline.length}`
                  : ""}
              />
            </ListItem>
          ))}
        </List>
      </Box>

      {/* Dialog для редактирования/создания точки */}
      <Dialog open={!!editing} onClose={() => setEditing(null)}>
        <DialogTitle>{editing?.idx != null ? "Редагування точки" : "Нова точка"}</DialogTitle>
        <DialogContent>
          <TextField
            label="Номер точки"
            margin="normal"
            fullWidth
            value={editing?.id || ""}
            onChange={e => setEditing(v => ({ ...v, id: e.target.value }))}
            disabled={!!editing?.qrImg}
          />
          <TextField
            label="Назва"
            margin="normal"
            fullWidth
            value={editing?.label || ""}
            onChange={e => setEditing(v => ({ ...v, label: e.target.value }))}
          />
          <TextField
            label="Опис"
            margin="normal"
            fullWidth
            value={editing?.description || ""}
            onChange={e => setEditing(v => ({ ...v, description: e.target.value }))}
          />
          <TextField
            label="X (%)"
            margin="normal"
            type="number"
            fullWidth
            value={editing?.x ?? ""}
            onChange={e => setEditing(v => ({ ...v, x: Number(e.target.value) }))}
          />
          <TextField
            label="Y (%)"
            margin="normal"
            type="number"
            fullWidth
            value={editing?.y ?? ""}
            onChange={e => setEditing(v => ({ ...v, y: Number(e.target.value) }))}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setEditing(null)}>Скасувати</Button>
          <Button
            variant="contained"
            onClick={async () => {
              await savePoint(editing);
              setEditing(null);
            }}
          >
            Зберегти
          </Button>
          {editing?.id && (
            <Button
              color="error"
              onClick={async () => {
                await deletePoint(editing.id);
                setEditing(null);
              }}
            >
              Видалити
            </Button>
          )}
        </DialogActions>
      </Dialog>

      {/* Dialog для редактирования названия маршрута */}
      <Dialog open={!!editingEdge} onClose={() => setEditingEdge(null)}>
        <DialogTitle>Редагування назви маршруту</DialogTitle>
        <DialogContent>
          <TextField
            label="Назва маршруту"
            value={editingEdgeName}
            onChange={e => setEditingEdgeName(e.target.value)}
            autoFocus
            fullWidth
            sx={{ mt: 2 }}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setEditingEdge(null)}>Скасувати</Button>
          <Button
            variant="contained"
            onClick={async () => {
              await updateEdgeName(editingEdge._id, editingEdgeName);
              setEditingEdge(null);
            }}
            disabled={!editingEdgeName.trim()}
          >
            Зберегти
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
}
