import React from "react";
import UserMap from "./InteractiveMap";
import AdminMap from "./AdminPoints";
import {
  Box, Button, Typography, Select, MenuItem, FormControl, InputLabel, Dialog, useMediaQuery
} from "@mui/material";
function App() {
  const [isAdmin, setIsAdmin] = React.useState(false);
  return (
    <div>
      <Button onClick={() => setIsAdmin(v => !v)} style={{ position: 'fixed', right: 24, top: 24, zIndex: 999 }}>
        {isAdmin ? "Перейти до карти" : "Адмін-режим"}
      </Button>
      {isAdmin ? <AdminMap /> : <UserMap />}
    </div>
  );
}

export default App;
